import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue';

export default {
	components: {
		uniLoadMore
	},
	data() {
		return {
			noticeInfo:[],
			source: 0,
			addressList: [],
			scrollHeight: 100,
			tabScrollTop: 0,
			contentText: {
				contentdown: '上拉加载更多',
				contentrefresh: '加载中',
				contentnomore: '我是有底线的'
			},
			page:1,
			limit:20,
			status: 'more',
		}
	},
	onLoad() {
		getApp().user_api(true);
		this.noticeIist();
		this.calcSize();
	},
	methods: {
		noticeIist(){
			let self = this;
			let path = '/gzh/activity_api/activity_list'
			let data = {page:self.page,limit:self.limit};
			getApp().http(path, data, function(res) {
				console.log('个人信息',res)
				
				let list = res;
				if (list.length > 0) {
					console.log(list.length)
					
					self.noticeInfo = self.noticeInfo.concat(list);
					self.status = list.length >= self.limit ? "more" : "";
					if (self.status == "more") {
						self.page += 1;
					}
					
					// self.status = list.length <= 20 ? "more" : "";
					// if (self.status == "more") {
					// 	self.page += 1;
					// }
					// self.noticeInfo = self.noticeInfo.concat(list);
				} else {
					self.status = "";
				}
				// self.noticeInfo = res;
			})
		},
		prize(item){
			console.log('水电费水电费的',item);
			
			uni.navigateTo({
				url:'/pages/prize/draw?id='+item.id
			})  
		},
		indexUrl(icon){
			let http = icon.indexOf('http')
			if(http == -1){
				return this.webUrl +'/'+icon
			}else{
				return icon;
			}
		},
		
		// 到底触发
		scroAddr(){
			let self = this;
			if(self.status){
				self.noticeIist();
			}
		},
		//计算右侧栏每个tab的高度等信息
		calcSize(){
			var self = this;
			uni.getSystemInfo({
				success: function(res) { // res - 各种参数
					let info = uni.createSelectorQuery().select(".contents");
					self.scrollHeight = res.windowHeight
					// self.scrollHeight = 200
					console.log(info);
					console.log('啊啊啊啊',res,self.scrollHeight);
				}
			});
		},

	}
}
